  addCategory(category: string, subcategory: string[]) {
    const categories = {
      category: category,
      subCategory: subcategory
    };
    this.http.post('api/addcategory', categories)
    .subscribe( result => {
       console.log(result);
       const CategoriesUpdates : CategoriesModel ={
         category: category,
         subCategory: subcategory
       };
       this.categories.push(CategoriesUpdates);
       this.updatedCategory.next([...this.categories]);
     }

    );
  }

