import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';

import {MatTableDataSource, MatPaginator, MatSort, MatSnackBar} from '@angular/material';

import { Subscription } from 'rxjs';
import { StaffModel } from '../../staff/model/staff-model';
import { StaffService } from '../../staff/staff.service';
import { StockService } from '../stock.service';
import { CategoriesModel } from '../category/category.model';
import { StocksModel } from '../model/stock.model';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-your-stock',
  templateUrl: './your-stock.component.html',
  styleUrls: ['./your-stock.component.css']
})
export class YourStockComponent implements OnInit, OnDestroy {

  private StockSub= new Subscription();
  displayedColumns = ['ProductImage', 'ProductName', 'Quantity' , 'Category',  'SubCategory', 'Description', 'ProductCode',
   'Date', 'Action'];
   isResultsLoading = false;
    stocks: StocksModel[] = [];
    dataSource;
    category: string;
    subcategory: string;
    categoryValue:string;
    categoryData: CategoriesModel[] = [];
    private map = new Map<string, string[]>();
    categoryControl = new FormControl('');
    subCategoryControl = new FormControl('');
    filterValues = {
      productCategory: '',
      productSubCategory: ''
    };

    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;



  constructor( private router: Router, private stockService: StockService, private snackBar: MatSnackBar ) {

  }

 saveBtnHandler() {
     this.router.navigate(['dashboard', 'addStock']);
            }
  onEdit(id: string) {
     this.router.navigate(['dashboard', 'addStock', id]);
     }

    // ngOnInit
  ngOnInit() {
    this.isResultsLoading = true;
    this.stockService.getCategory();
    this.stockService.updatedCategoryListener().
    subscribe( (result: CategoriesModel[]) => {
       this.categoryData = result;
       result.forEach((value: CategoriesModel) => {
         console.log(value);
         this.map.set(value.category, value.subCategory);
       });
    }
    , errorResponse => { console.log(errorResponse); }
    );
    this.stockService.getStock();
    this.stockService.getStockUpdateListener().subscribe((result: StocksModel[]) => {
      this.stocks = result;
      console.log(this.dataSource);
      this.dataSource = new MatTableDataSource(this.stocks);
      this.dataSource.filterPredicate = this.createFilter();
      this.dataSource.paginator = this.paginator;
      this.isResultsLoading = false;

  });
  this.categoryControl.valueChanges
  .subscribe(
    category => {
      this.filterValues.productCategory = category;
      console.log(category);
      // this.category = category;
      this.dataSource.filter = JSON.stringify(this.filterValues);
      this.categoryValue = category;
    }
  );
this.subCategoryControl.valueChanges
  .subscribe(
    subCategory => {
      this.filterValues.productSubCategory = subCategory;
      this.dataSource.filter = JSON.stringify(this.filterValues);
    }
  );
}

// get Categories
get categories(): string[] {
  return Array.from(this.map.keys());
}

// get subCategories
get subcategories(): string[] | undefined {
  return this.map.get(this.categoryValue);
}

// ngOnDestroy
ngOnDestroy() {
  this.StockSub.unsubscribe();
}

// Delete stock
 onDelete(id: string) {
   this.stockService.deleteStock(id);
   this.snackBar.open('Deleted', 'ok' , {
    duration: 2000,
  });
   console.log('triggered delete wit id' + id);
 }

 // filter
 createFilter(): (data: StocksModel, filter: string) => boolean {
  let filterFunction = function(data, filter): boolean {
    let searchTerms = JSON.parse(filter);
    return data.productCategory.toLowerCase().indexOf(searchTerms.productCategory) !== -1
      && data.productSubCategory.toString().toLowerCase().indexOf(searchTerms.productSubCategory) !== -1

  };
  return filterFunction;
}



}



