import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';
import { StaffService } from '../staff.service';
import { Router, ParamMap, ActivatedRoute } from '@angular/router';
import { StaffModel } from '../model/staff-model';
import { mimeType } from '../../mime-type.validator';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-add-staff',
  templateUrl: './add-staff.component.html',
  styleUrls: ['./add-staff.component.css']

})
export class AddStaffComponent implements OnInit {
  private mode = 'create';
  private staffId: string;
  staffProfile: FormGroup;
  staff: StaffModel;
  imagePreview: string;
  private isImage = 'true';

  constructor(private staffService: StaffService, private routs: ActivatedRoute, private snackBar: MatSnackBar) { }



  ngOnInit() {
    this.staffProfile =  new FormGroup({
      name: new FormControl('', [Validators.required]),
      phoneNumber: new FormControl('', [Validators.required]),
      gender: new FormControl('', [Validators.required]),
      address: new FormControl('', [Validators.required]),
      description: new FormControl(''),
      profile: new FormControl('', [Validators.required]),
      date: new FormControl('', [Validators.required]),
      salary: new FormControl('', [Validators.required]),
      image: new FormControl('', {validators: [Validators.required], asyncValidators: [mimeType]})
      });
    this.routs.paramMap.subscribe((paramMap: ParamMap) => {
     if (paramMap.has('staffId')) {
      this.mode = 'edit';
      this.staffId = paramMap.get('staffId');
      this.staffService.getStaffOne(this.staffId).subscribe(staffData => {
        console.log("here is staff data "+staffData);
        this.staff = {_id: staffData._id, name: staffData.name, phoneNumber: staffData.phoneNumber,
                      gender: staffData.gender, address: staffData.address, description: staffData.description
                      , profile: staffData.profile, date: staffData.date, salary: staffData.salary, imagePath: staffData.imagePath,
                       salaryStatus: null}
        this.staffProfile.setValue({
          name: this.staff.name,
          phoneNumber: this.staff.phoneNumber,
          gender: this.staff.gender,
          address: this.staff.address,
          description: this.staff.description,
          profile: this.staff.profile,
          date: this.staff.date,
          salary: this.staff.salary,
          image: this.staff.imagePath,
          salaryStatus: null
     });
    });
    } else {
      this.mode = 'create';
      this.staffId = null;
    }
 });

}

onFileUpload(event: Event) {
 const file = (event.target as HTMLInputElement).files[0];
 this.staffProfile.patchValue({image: file});
 this.staffProfile.get('image').updateValueAndValidity();
 const render = new FileReader();
 render.onload = () => {
   this.imagePreview = render.result as string;
 };
 render.readAsDataURL(file);
 console.log(file);
}

onSubmit(formDirective: FormGroupDirective) {
  if (this.mode === 'create') {
    console.log(this.staffProfile.controls.image);
  if (this.staffProfile.invalid) {
    this.isImage = 'false';
  return;
} else {
  this.staffService.addPost(this.staffProfile, this.staffProfile.value.image, 'pending');
  this.snackBar.open('Added', 'ok' , {
    duration: 2000,
  });

 console.log(this.staffProfile);

}
formDirective.resetForm();
this.staffProfile.reset();
} else {
  this.staffService.updatePost(this.staffProfile, this.staffId , this.staffProfile.value.image);
  this.snackBar.open('Updated', 'ok' , {
    duration: 2000,
  });
}
}
}



