import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import {HttpClientModule, HTTP_INTERCEPTORS} from '@angular/common/http';


import { AppComponent } from './app.component';
import { SideNavComponent } from './side-nav/side-nav.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToolBarComponent } from './tool-bar/tool-bar.component';
import {AngularMaterialModule} from './shared/angular-material.module';
import {AppRoutingModule} from './app-routing.module';

import { from } from 'rxjs/observable/from';
import { YourStaffComponent } from './staff/your-staff/your-staff.component';
import { StaffInformationComponent } from './staff/staff-information/staff-information.component';
import { StaffSalaryComponent } from './staff/staff-salary/staff-salary.component';
import { AddStaffComponent } from './staff/add-staff/add-staff.component';
import { StaffService } from './staff/staff.service';
import { YourStockComponent } from './stock/your-stock/your-stock.component';
import { LoginPageComponent } from './auth/login-page/login-page.component';
import { SignupPageComponent } from './auth/signup-page/signup-page.component';
import { AuthService } from './auth/auth-services/auth.service';
import { AuthInterceptor } from './auth/auth-interceptor';
import { AuthGuard } from './auth.guard';
import { CategoryComponent } from './stock/category/category.component';
import { StockService } from './stock/stock.service';
import { AddStockComponent } from './stock/add-stock/add-stock.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { HomeComponent } from './home/home.component';


@NgModule({
  declarations: [
    AppComponent,
    SideNavComponent,
    ToolBarComponent,
    YourStaffComponent,
    StaffInformationComponent,
    StaffSalaryComponent,
    AddStaffComponent,
    YourStockComponent,
    LoginPageComponent,
    SignupPageComponent,
    CategoryComponent,
    AddStockComponent,
    AboutUsComponent,
    HomeComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AngularMaterialModule,
    AppRoutingModule,
    ReactiveFormsModule,
    HttpClientModule,
    FormsModule
  ],
  providers: [StaffService, AuthService, AuthGuard, StockService,
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true }],
  bootstrap: [AppComponent]
})
export class AppModule { }



