import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { YourStaffComponent } from './staff/your-staff/your-staff.component';
import { AppComponent } from './app.component';
import { StaffInformationComponent } from './staff/staff-information/staff-information.component';
import { StaffSalaryComponent } from './staff/staff-salary/staff-salary.component';
import { AddStaffComponent } from './staff/add-staff/add-staff.component';
import { LoginPageComponent } from './auth/login-page/login-page.component';
import { SideNavComponent } from './side-nav/side-nav.component';
import { SignupPageComponent } from './auth/signup-page/signup-page.component';
import { AuthGuard } from './auth.guard';
import { YourStockComponent } from './stock/your-stock/your-stock.component';
import { CategoryComponent } from './stock/category/category.component';
import { AddStockComponent } from './stock/add-stock/add-stock.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { HomeComponent } from './home/home.component';
const routes: Routes = [
  { path: '', redirectTo: '/dashboard/home', pathMatch: 'full' },
  {
    path : 'login',
    component: LoginPageComponent
  },
  {
  path : 'sign-up',
  component: SignupPageComponent
  },
  {
  path : 'about-us',
  component: AboutUsComponent
  },
  {
    path : 'dashboard',
    component: SideNavComponent,
    canActivate: [AuthGuard],
    children : [
      {
        path : 'home',
        component: HomeComponent,
       },
      {
        path : 'yourstaff',
        component: YourStaffComponent,
       },

       {
         path : 'staff-information',
         component: StaffInformationComponent
        },
        {
          path : 'staff-salary',
          component: StaffSalaryComponent
         },
         {
           path : 'add-staff',
           component: AddStaffComponent
          },
          {
            path : 'add-staff/:staffId',
            component: AddStaffComponent,
           },
           {
            path : 'yourStock',
            component: YourStockComponent,
           },
           {
            path : 'yourCategory',
            component: CategoryComponent,
           },
           {
            path : 'addStock',
            component: AddStockComponent,
           },
           {
            path : 'addStock/:stockId',
            component: AddStockComponent,
           },

    ]
  },


];
@NgModule({
  imports: [

   RouterModule.forRoot(routes)
    ],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }



