import { Component, OnInit, OnDestroy, ViewEncapsulation } from '@angular/core';
import { StaffService } from '../staff.service';
import { StaffModel } from '../model/staff-model';
import { Subscription } from 'rxjs';
import { FormGroup } from '@angular/forms';
import { MatTableDataSource, MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';


@Component({
  selector: 'app-staff-information',
  templateUrl: './staff-information.component.html',
  styleUrls: ['./staff-information.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class StaffInformationComponent implements OnInit, OnDestroy {

 private staffInformation: StaffModel[] = [];
 private StaffSub = new Subscription();
 dataSource;
 isResultsLoading = false;
 staffSalary: FormGroup;


  constructor(private staffService: StaffService, private snackBar: MatSnackBar, private router: Router) { }

  ngOnInit() {
    this.isResultsLoading = true;
  this.staffService.getStaff();
  this.staffService.getStaffUpdateListener().subscribe(result => {
       this.staffInformation = result;
       this.dataSource = new MatTableDataSource(this.staffInformation);
       console.log('this is staff information' + this.staffInformation);
       this.isResultsLoading = false;
  });
  }
  onDelete(id: string) {
    this.staffService.deleteStaff(id);
  //   this.staffService.getStaffUpdateListener().subscribe(result => {
  //     this.staffData = result;
  //     console.log(this.dataSource);
  //     console.log("result"+result);
  //     this.dataSource = new MatTableDataSource(this.staffData);
  //     this.dataSource.paginator = this.paginator;

  // });
  this.snackBar.open('Deleted', 'ok' , {
    duration: 2000,
  });

    console.log('triggered delete wit id' + id);
  }
  onEdit(id: string) {
    this.router.navigate(['dashboard', 'add-staff', id]);
     }

  ngOnDestroy() {
  this.StaffSub.unsubscribe();
  }


}



