import { Component, OnInit, OnDestroy } from '@angular/core';
import { StockService } from '../stock.service';
import { CategoriesModel } from '../category/category.model';
import { Subscription } from 'rxjs';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';
import { StocksModel } from '../model/stock.model';
import { mimeType } from '../../mime-type.validator';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-add-stock',
  templateUrl: './add-stock.component.html',
  styleUrls: ['./add-stock.component.css']
})
export class AddStockComponent implements OnInit, OnDestroy {


  categoryData: CategoriesModel[] = [];
  stockData: StocksModel[] = [];
  private map = new Map<string, string[]>();
  subCategoryData: string[] = null;
   categorySub = new Subscription();
   stockInformation: FormGroup;
   category: string;
   subcategory: string;
   imagePreview: string;
   private mode = 'create';
   private stockId: string;
   stock: StocksModel;
   subCategoryCheck: string;
   description: string;
   private isImage = 'true';
  constructor( private stockService: StockService, private routs: ActivatedRoute, private snackBar: MatSnackBar) { }

  ngOnInit() {
     this.stockInformation = new FormGroup({
     productName: new FormControl ('', [Validators.required]),
     productQuantity: new FormControl ('', [Validators.required]),
     productCategory: new FormControl ('', [Validators.required]),
     productSubCategory: new FormControl (''),
     productDescription: new FormControl (''),
     productDate: new FormControl ('', [Validators.required]),
     productCode: new FormControl ('', [Validators.required]),
     image: new FormControl('', {validators: [Validators.required], asyncValidators: [mimeType]})
     });

    this.stockService.getCategory();
    this.stockService.updatedCategoryListener().
    subscribe( (result: CategoriesModel[]) => {
       this.categoryData = result;
       result.forEach((value: CategoriesModel) => {
         console.log(value);
         this.map.set(value.category, value.subCategory);
       });
    });
    console.log(this.map);
    this.routs.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has('stockId')) {
       this.mode = 'edit';
       this.stockId = paramMap.get('stockId');
       this.stockService.getStockOne(this.stockId).subscribe(stockData => {
         console.log('here is stock data' + stockData);
         console.log(stockData.productSubCategory);
         if (stockData.productDescription) {
          this.description = stockData.productDescription;
        } else {
          this.description = '';

        }
         if (stockData.productSubCategory) {
           this.subcategory = stockData.productSubCategory;
           this.subCategoryCheck = stockData.productSubCategory;
         } else {
           this.subCategoryCheck = '';

         }
         console.log(this.subCategoryCheck);
         this.stock = {_id: stockData._id, productName: stockData.productName, productQuantity: stockData.productQuantity,
        productCategory: stockData.productCategory, productSubCategory: stockData.productSubCategory,
      productDescription: stockData.productDescription, date: stockData.date, productCode: stockData.productCode,
       imagePath: stockData.imagePath};
         this.stockInformation.setValue({
           productName: this.stock.productName,
           productQuantity: this.stock.productQuantity,
           productCategory: this.stock.productCategory,
           productSubCategory: this.subCategoryCheck,
           productDescription: this.description,
           productDate: this.stock.date,
           productCode: this.stock.productCode,
           image: this.stock.imagePath,

      });
     });
     } else {
       this.mode = 'create';
       this.stockId = null;
     }
  });
  }
  get categories(): string[] {

    return Array.from(this.map.keys());
  }

  get subcategories(): string[] | undefined {
    return this.map.get(this.category);
  }
  ngOnDestroy() {
    this.categorySub.unsubscribe();
  }
  onFileUpload(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.stockInformation.patchValue({image: file});
    this.stockInformation.get('image').updateValueAndValidity();
    const render = new FileReader();
    render.onload = () => {
      this.imagePreview = render.result as string;
    };
    render.readAsDataURL(file);
    console.log(file);
   }

  onSubmit(formDirective: FormGroupDirective) {
    if (this.mode === 'create') {
    if (this.stockInformation.invalid) {
      this.isImage = 'false';
      return;
    } else {
      this.stockService.addPost(this.stockInformation, this.stockInformation.value.image);
     console.log('inside stock add' + this.stockInformation.value.productDate);
     this.snackBar.open('Added', 'ok' , {
      duration: 2000,
    });
    }
    formDirective.resetForm();
    this.stockInformation.reset();
    } else {
      this.stockService.updatePost(this.stockInformation, this.stockId , this.stockInformation.value.image);
      this.snackBar.open('Updated', 'ok' , {
        duration: 2000,
      });
    }
  }

}



