import { Injectable, ErrorHandler } from '@angular/core';
import { HttpClient, HttpInterceptor, HttpErrorResponse } from '@angular/common/http';
import { FormGroup } from '@angular/forms';
import { AuthModel } from '../auth-models/auth.model';
import { Router } from '@angular/router';
import { catchError } from 'rxjs/operators';
import { Subject } from 'rxjs';



@Injectable()
export class AuthService {

  public token: string;
  public userId: string;
  private message = new Subject<string>();

  constructor( private httpClient: HttpClient, private router: Router ) { }

  getToken() {
    console.log("returning token from here "+this.token);
    return localStorage.getItem('token');
  }
  userSignUp(formGroup: FormGroup ) {
    const user: AuthModel =  { email: formGroup.value.email,
                              password: formGroup.value.matching_passwords.password, name: formGroup.value.name };
    this.httpClient.post<{result: AuthModel[], message: String}>('api/signup', user)
    .subscribe(res => {
      console.log(res.result);
      console.log(res);
      this.router.navigate(['/login']);
    },
    (error: HttpErrorResponse) => {
      console.log(error.error.message);
      if (error.error.message === 'User validation failed: email: unique') {
        this.message.next('user already exist with this email address');
      }
    });

      }
  getMessageListner() {
    return this.message.asObservable();
  }
  userLogin(formGroup: FormGroup ) {
    const user: AuthModel =  { email: formGroup.value.email,
                              password: formGroup.value.password, name: null };
    this.httpClient.post<{token: string, result:AuthModel}>('api/login', user)
    .subscribe(res => {

      // console.log('here is response'+res.token);
      console.log('here is response' + res.result.email);
      const token  = res.token;
      this.token = token;
      this.userId = res.result.name;
       localStorage.setItem('token', token);
       localStorage.setItem('userId', this.userId);
      console.log('i am from login token ' + token);

      this.router.navigate(['/dashboard', 'home']);
    },
    (error: HttpErrorResponse) => {
      console.log(error.error.message);
      this.message.next(error.error.message);

    } );

      }

    loggedIn ()  {
      return !!localStorage.getItem('token');
    }

    UserId ()  {
      return localStorage.getItem('userId');
    }

    logOutUser() {
    localStorage.removeItem('token');
    localStorage.removeItem('userId');
    this.router.navigate(['/login']);
    }


}



