import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { FormControl , FormGroup , Validators, FormGroupDirective} from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { StaffService } from '../staff.service';
import { StaffModel } from '../model/staff-model';

import { MatTableDataSource, MatPaginator, MatSort, MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-staff-salary',
  templateUrl: './staff-salary.component.html',
  styleUrls: ['./staff-salary.component.css']
})
export class StaffSalaryComponent implements OnInit , OnDestroy {

  private StaffSub= new Subscription();
  displayedColumns = ['Image', 'Name', 'PhoneNumber' , 'Salary',  'Status', 'Update'];
   staffData: StaffModel[] = [];
   staffDataOne: StaffModel[] = [];
    dataSource;
    isResultsLoading = false;
    check = false;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    staffSalary: FormGroup;
    staffId: string;
    staff: StaffModel;


  constructor( private staffService: StaffService, private snackBar: MatSnackBar) { }

  ngOnInit() {
    this.staffSalary =  new FormGroup({
      name: new FormControl('', [Validators.required]),
      salary: new FormControl('', [Validators.required]),
      checkStatus: new FormControl('', [Validators.required]),
      });
      this.isResultsLoading = true;
      this.staffService.getStaff();
    this.staffService.getStaffUpdateListener().subscribe(result => {
      this.staffData = result;
      console.log(this.dataSource);
      this.dataSource = new MatTableDataSource(this.staffData);
      this.dataSource.paginator = this.paginator;
       this.isResultsLoading= false;


  });
  }
  ngOnDestroy() {
    this.StaffSub.unsubscribe();
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
    console.log(filterValue);
  }
  onClick(id: string) {
    console.log(id);
    this.isResultsLoading= true;
    this.staffId = id;
    this.staffService.getStaffOne(id).subscribe( (staffData) => {
      this.staff = {_id: staffData._id, name: staffData.name, phoneNumber: staffData.phoneNumber,
        gender: staffData.gender, address: staffData.address, description: staffData.description
        , profile: staffData.profile, date: staffData.date, salary: staffData.salary, imagePath: staffData.imagePath,
         salaryStatus: null}
     this.staffSalary.setValue(
       {
        name: staffData.name,
        salary: staffData.salary,
        checkStatus: null
       }
     );
    }

    );
    this.isResultsLoading= false;
   return this.check = true;
 }
 onSubmit(formGroupDirective: FormGroupDirective) {

   console.log(" staff data "+this.staffSalary.value.checkStatus);
this.staffService.updateSalaryStatus(this.staff, this.staffSalary.value.checkStatus, this.staffId);
this.snackBar.open('updated', 'ok' , {
  duration: 2000,
});
this.check = false;
 }
}



