import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {MatButtonModule,
  MatIconModule,
  MatToolbarModule,
  MatListModule,
  MatCardModule,
  MatTableModule,
  MatMenuModule,
  MatFormFieldModule,
  MatInputModule,
  MatDatepickerModule,
  MatNativeDateModule,
  MatSnackBarModule,
  MatPaginatorModule,
  MatProgressSpinnerModule,
  MatSortModule,
MatSelectModule,
MatGridListModule,
MatAutocompleteModule,
MatChipsModule,
MatExpansionModule,
MatTabsModule,
MatTooltipModule,
MatDividerModule} from '@angular/material';

import {MatSidenavModule} from '@angular/material/sidenav';

const exportedMatModules = [
  MatButtonModule,
  MatIconModule,
  MatSidenavModule,
  MatToolbarModule,
  MatListModule,
  MatCardModule,
  MatTableModule,
  MatMenuModule,
  MatFormFieldModule,
  MatInputModule,
  MatDatepickerModule,
  MatNativeDateModule,
  MatSnackBarModule,
  MatPaginatorModule,
  MatProgressSpinnerModule,
  MatSortModule,
  MatSelectModule,
  MatGridListModule,
  MatAutocompleteModule,
  MatChipsModule,
  MatExpansionModule,
  MatTabsModule,
  MatTooltipModule,
  MatDividerModule
];

@NgModule({
  imports: [
    CommonModule,
    ...exportedMatModules
  ],
  exports : [
    ...exportedMatModules
  ],
  declarations: []
})
export class AngularMaterialModule { }



