import { Component, OnInit, ViewEncapsulation, OnDestroy } from '@angular/core';
import { StockService } from '../stock.service';
import { CategoriesModel } from './category.model';
import { Subscription } from 'rxjs';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class CategoryComponent implements OnInit, OnDestroy {
  private CategorySub= new Subscription();
  category = '';
  subCategory= [];
  result: CategoriesModel[];
  categoryForm: FormGroup;

  constructor( private stockService: StockService) { }

  ngOnInit() {

this.categoryForm = new FormGroup({
  category: new FormControl('', [Validators.required]),
  subCategory: new FormControl('', [Validators.required]),
});
this.stockService.getCategory();
this.stockService.updatedCategoryListener().
subscribe( (result: CategoriesModel[]) => {
 this.result = result;
 console.log(this.result);
});
  }
  remove(subCategory: string) {
    const index = this.subCategory.indexOf(subCategory);
    if (index > -1) {
      this.subCategory.splice(index, 1);
   }
  }
  onClick() {

  }
  addSub(newCat: string) {
    if (newCat) {
      this.subCategory.push(newCat);
    }
    console.log(this.subCategory);
  }
  onSubmit(formDirective: FormGroupDirective) {

     this.stockService.addCategory(this.categoryForm.value.category, this.subCategory);
     this.subCategory = [];
     formDirective.resetForm();
}
ngOnDestroy() {
  this.CategorySub.unsubscribe();
}
onDelete(category: string) {
  this.stockService.deleteCategory(category);
}
}



